@import '~node_modules/modern-normalize/modern-normalize.css';


html {
    scroll-behavior: smooth;
}
body {
    background-color: #E5E5E5;
    margin-left: auto;
    margin-right: auto;
    font-family: Roboto, sans-serif;
    width: 100%;
}


img {
    display: block;
    max-width: 100%;
    height: auto;
}
a {
    text-decoration: none;
    color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

button {
    cursor: pointer;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}